.org-project {
    display: flex;
    flex-direction: column;
}

.project-header {
    display: flex;
    flex-direction: row;
    margin: 10px auto;
}

.project-header .project-icon {
    display: inline-block;
    width: 50px;
}

.project-header .project-icon img {
    width: 50px;
}


.project-header .project-title {
    display: inline-block;
    font-size: 20px;
}

.project-header .project-year {
    display: inline-block;
    font-size: 11px;
}

.project-header .project-tech-stack {
    display: inline-block;
    font-size: 11px;
}

.project-title-context {
    display: flex;
    flex-direction: column;
    padding: 0 10px;
}

.project-tech-stack .tech-stack-item {
    border-left: 1px solid #08fdd8;
    display: inline;
    padding: 0 5px;
}

.project-tech-stack .tech-stack-item:first-of-type {
    border-left: none;
}