
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar {
  height: 20vh;
  width: 100%;
}

.navbar > ul {
  list-style-type: none;
  padding: 10px;
}

.navbar .material-icons {
  cursor: pointer;
  font-size: 35px;
  margin: 0 85px;
  color: #fff;
}

.navbar .material-icons:hover {
  color: #08fdd8; 
}


.navbar .active .material-icons {
  color: #413d3d;
}

.sticky-profile {
  position: sticky;
  top: 0;
  background-color: #1d1d1d;
  color: #08fdd8;
}

.page {
  height: 100vh;
  width: 100%;
  background-color: #1d1d1d;
  color: #fff;
}

.page.home-page {
  text-align: center;
  vertical-align: middle;
}

.page .about {
  font-size: 20px;
  padding: 25% 10%;
  text-align: left;
  line-height: 1.3;
}

.page .link {
  cursor: pointer;
  color: #08fdd8;
}

.page.experience-page {
  text-align: center;
}

.page .experience-container {
  padding: 10% 2%;
}

.page .contact-container {
  padding: 10% 2%;
}

.page.contact-page {
  text-align: center;
}

.text-content {
  display: inline-block;
  width: 55%;
}

.tagContainer {
  display: inline-block;
  width: 45%;
}

.tagContainer .tagcloud {
  width: auto;
  height: auto;
  position: relative;
}

.tagcloud--item {
  color: #08fdd8;
}

.profile {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: auto 20%;
}

.profile-pic {
  display: inline-block;
}
.profile-info {
  display: inline-block;
  margin: 0 10%;
}
.profile-pic img {
  width: 200px;
  height: 200px;
  border-radius: 59%;
  display: inline-block;
  border: 1px solid #fff;
}

.profile-info .name {
  font-size: 40px;
  margin: 5% auto;
  display: block;
}

.profile-info .desc {
  font-size: 20px;
  margin: 5% auto;
  display: block;
}

.profile-info .desc-tags {
  font-size: 15px;
  margin: 5% auto;
  display: block;
}
.profile-header {
  background-color: #1d1d1d;
  border-bottom: 1px solid #08fdd847;
  padding-bottom: 10px;
}

.org-logo {
  width: 60px;
  border-radius: 50%;
}

.stickers {
  width: 200px;
  position: fixed;
  right: 0;
  top: 0;
  display: flex;
  flex-wrap: wrap;
}

span.sticker-container {
  margin: 10px;
}

span.sticker-container > img {
  height: 50px;
  width: 75px;
}

.contact-container {
  margin: 0 25%;
}

.contact-container  a {
  color: #08fdd8;
}

.contact-icons {
  width: 100px;
  height: 50px;
  margin: 5px;
}

.contact-icons:hover {
  border-bottom: 1px solid;
}

.contact-icons.github {
  background-color: #fff;
  height: 40px;
}

.contact.email {
  align-content: center;
  align-items: center;
  margin: 0 30%;
  display: flex;
  margin-top: 15px;
  cursor: pointer;
}